import { ReactNode, useContext, useEffect, useRef } from 'react';
import { LngLatBounds, MapState } from '../../types';
import { BackendDataContext } from '../BackendData/context';
import { MapContext } from './context';
import {createArcGeometry} from "../../utils";

export interface MapProps {
    initialState: MapState;
    onClick: (p: { lngLat: number[] }) => void;
    onMouseMove: (p: { lngLat: number[] }) => void;
    onMoveEnd: (p: { center: number[]; zoom: number; bounds: LngLatBounds }) => void;
    onMoveStart: () => void;
    children?: ReactNode;
    minimized: boolean;
    center: number[];
    centerAzimuth: number
}

export const MapProvider = (p: MapProps) => {
    const { lines } = useContext(BackendDataContext);
    const mapRef = useRef<mapgl.Map>();


    useEffect(() => {
        if (!mapRef.current || !p.minimized) {
            return;
        }

        const marker = new mapgl.Marker(mapRef.current, {
            coordinates: p.center,
            icon: 'data:image/svg+xml;base64,' + btoa(icon),
            anchor: [8, 8],
            size: [16, 16],
            interactive: false,
            zIndex: 5,
        });

        const polygon = new mapgl.Polygon(mapRef.current, {
            coordinates: createArcGeometry(p.center, p.centerAzimuth, 45, 50),
            color: '#aa00ff66',
            strokeColor: '#aa00ff66',
            strokeWidth: 1,
            interactive: false,
        });

        return () => {
            marker.destroy();
            polygon.destroy();
        };
    }, [p.center, p.minimized]);

    useEffect(() => {
        if (mapRef.current) {
            // redraw after CSS animation transition
            setTimeout(() => mapRef.current?.invalidateSize(), 300);
        }
    }, [p.minimized])

    useEffect(() => {
        const map = new mapgl.Map('map', {
            // center: p.initialState.center,
            zoom: p.initialState.zoom,
            key: window.mapgl_key,
            disablePitchByUserInteraction: true,
            disableRotationByUserInteraction: true,
            useRtlTextPlugin: 'always-on',
        });


        map.on('styleload', () => {
            map.addLayer({
                id: 'my-lines',
                type: 'line',
                filter: ['==', ['sourceAttr', 'foo'], 'bar'],
                style: {
                    width: 2,
                    color: '#ff000055',
                },
            });
        });

        const bounds = map.getBounds();
        p.onMoveEnd({
            center: map.getCenter(),
            zoom: map.getZoom(),
            bounds: { southWest: bounds.southWest, northEast: bounds.northEast },
        });


        mapRef.current = map;

        return () => {
            map.destroy();
        };
    }, []);


    useEffect(() => {
        console.log('SetCenter', p.center);
        mapRef.current?.setCenter(p.center);
    }, [p.center])

    useEffect(() => {
        const map = mapRef.current;
        if (!map) {
            return;
        }

        map.on('click', p.onClick);
        map.on('mousemove', p.onMouseMove);
        map.on('movestart', p.onMoveStart);

        const moveendListener = () => {
            const bounds = map.getBounds();

            p.onMoveEnd({
                center: map.getCenter(),
                zoom: map.getZoom(),
                bounds: { southWest: bounds.southWest, northEast: bounds.northEast },
            });
        };
        map.on('moveend', moveendListener);

        return () => {
            map.off('click', p.onClick);
            map.off('mousemove', p.onMouseMove);
            map.off('movestart', p.onMoveStart);
            map.off('moveend', moveendListener);
        };
    }, [mapRef.current, p.onClick, p.onMouseMove, p.onMoveEnd, p.onMoveStart]);

    const sourceRef = useRef<mapgl.GeoJsonSource>();
    useEffect(() => {
        const map = mapRef.current;
        if (!map) {
            return;
        }
        const source = new mapgl.GeoJsonSource(map, {
            data: {
                type: 'FeatureCollection',
                features: [],
            },
            attributes: {
                foo: 'bar',
            },
        });
        sourceRef.current = source;
        return () => {
            source.destroy();
        };
    }, [mapRef.current]);

    useEffect(() => {
        if (sourceRef.current) {
            sourceRef.current.setData(lines);
        }
    }, [sourceRef.current, lines]);

    return <MapContext.Provider value={{ map: mapRef.current }}>{p.children}</MapContext.Provider>;
};

const icon = `<svg xmlns="http://www.w3.org/2000/svg" width="10" height="10" viewBox="0 0 10 10">
<circle fill="#aa00ff" cx="5" cy="5" r="5"/>
<circle fill="#ffffff" cx="5" cy="5" r="2"/>
</svg>`;
