import { useContext, useEffect } from 'react';
import { BackendPointData } from '../../types';
import { MapContext } from '../map/context';
import {createArcGeometry} from "../../utils";

const icon = `<svg xmlns="http://www.w3.org/2000/svg" width="10" height="10" viewBox="0 0 10 10">
<circle fill="#0000ff" cx="5" cy="5" r="5"/>
<circle fill="#ffffff" cx="5" cy="5" r="2"/>
</svg>`;

export const useCamera = (p: { point?: { lngLat: number[]; properties: BackendPointData } }) => {
    const { map } = useContext(MapContext);

    useEffect(() => {
        if (!map || !p.point) {
            return;
        }

        const marker = new mapgl.Marker(map, {
            coordinates: p.point.lngLat,
            icon: 'data:image/svg+xml;base64,' + btoa(icon),
            anchor: [8, 8],
            size: [16, 16],
            interactive: false,
            zIndex: 5,
        });

        const polygon = new mapgl.Polygon(map, {
            coordinates: createArcGeometry(p.point.lngLat, p.point.properties.azimuth, 45, 50),
            color: '#0000ff66',
            strokeColor: '#0000ff66',
            strokeWidth: 1,
            interactive: false,
        });

        return () => {
            marker.destroy();
            polygon.destroy();
        };
    }, [map, p.point]);
};

