import {useContext, useEffect, useMemo, useRef, useState} from 'react';
import {findNearest, TrackPoint} from '../../utils';
import {BackendDataContext} from '../BackendData/context';
import * as classes from './styles.module.css';
import {CSSTransition, TransitionGroup,} from 'react-transition-group';
import {useHotkeys} from "react-hotkeys-hook";
import {getSimilarTracks} from "../../wfs";

export interface TrackProps {
    lngLat: number[];
    trackHint?: string;
    onClose: () => void;
    onMove: (point: TrackPoint | undefined) => void;
    onSwitch: (lngLat: number[], trackHint: string) => void;
    backToCityLens: () => void;
}


export const Track = ({lngLat, onClose, onSwitch, trackHint, onMove, backToCityLens}: TrackProps) => {
    const {find, findLinePoints} = useContext(BackendDataContext);
    const [running, setRunning] = useState(false);
    const [similarHidden, setSimilarHidden] = useState(false);
    const [panoramaMode, setPanoramaMode] = useState(false);
    const viewerRef = useRef();

    useEffect(() => {
        let timer = setTimeout(() => {
            if (running) {
                nextPoint && changePoint(nextPoint)
            }
        }, 550);
        return () => clearTimeout(timer);
    })


    useEffect(() => {
        let result = findNearest(lngLat, closePoints, trackHint);
        changePoint(result);
    }, [lngLat, trackHint])

    const closePoints = useMemo(() => {
        return find({lngLat, radius: 5000});
    }, [lngLat, trackHint]);

    const [currentPoint, setCurrentPoint] = useState(() => {
        return findNearest(lngLat, closePoints, trackHint);
    });


    useEffect(() => {
        if (panoramaMode) {
            setTimeout(() => {
                let yaw = 0;
                let pitch = 0;
                let hfov = 120;
                if (viewerRef.current) {
                    yaw = (viewerRef.current as any).getYaw();
                    pitch = (viewerRef.current as any).getPitch();
                    hfov = (viewerRef.current as any).getHfov();
                }
                viewerRef.current = (window as any).pannellum.viewer('panorama', {
                    "type": "equirectangular",
                    "autoLoad": true,
                    "panorama": currentPoint?.properties.photo_url,
                    "yaw": yaw,
                    "pitch": pitch,
                    "hfov": hfov,
                });
            })
        }
    }, [panoramaMode, currentPoint]);

    const changePoint = (point: TrackPoint | undefined) => {
        console.log('CHANGE POINT', point);
        if (!point) {
            onClose();
            return;
        } else {
            setCurrentPoint(point);
            if (point?.properties.panoramic) {
                setPanoramaMode(true);
            }
            onMove(point);
        }
    }

    useHotkeys(['up', 'right'], () => {
        (!running && nextPoint) && changePoint(nextPoint);
    }, {}, [currentPoint]);
    useHotkeys(['down', 'left'], () => {
        (!running && prevPoint) && changePoint(prevPoint);
    }, {}, [currentPoint]);

    const {nextPoint, prevPoint} = useMemo(() => {
        if (!currentPoint) {
            return {nextPoint: undefined, prevPoint: undefined};
        }
        const points = findLinePoints(currentPoint);
        if (!points) {
            return {nextPoint: undefined, prevPoint: undefined};
        }
        const index = points.findIndex(({properties}) => properties === currentPoint.properties);
        const nextFeature = points[index + 1];
        const nextPoint = nextFeature
            ? {lngLat: nextFeature.geometry.coordinates, properties: nextFeature.properties}
            : undefined;

        // prefetch frame images to improve interaction
        for (let i = index - 1; i < index + 2; i++) {
            if (i >= 0 && i < points.length) {
                new Image().src = points[i].properties.photo_url;
            }
        }

        const prevFeature = points[index - 1];
        const prevPoint = prevFeature
            ? {lngLat: prevFeature.geometry.coordinates, properties: prevFeature.properties}
            : undefined;
        return {nextPoint, prevPoint};
    }, [currentPoint]);

    if (!currentPoint) {
        return null;
    }

    const [similar, setSimilar] = useState<SimilarTrackProps[]>([]);

    useEffect(() => {
        if (running) {
            setSimilar([]);
        } else {
            const fetchData = async () => {
                const frameId = currentPoint.properties.id;
                const res = await getSimilarTracks(frameId);
                const result = await res.json() as any;
                let tracks = []
                for (let feature of result.features) {
                    tracks.push({
                        id: feature.properties.track_id,
                        key: feature.properties.track_id,
                        date: feature.properties.capture_time,
                        url: feature.properties.photo_url,
                        lngLat: [feature.geometry.coordinates[1], feature.geometry.coordinates[0]],
                        onSwitch: onSwitch
                    });
                }
                setSimilar(tracks);
            };
            fetchData().catch(console.error);
        }

    }, [running, currentPoint])

    const properties = currentPoint.properties;

    return (
        <div className={classes.trackPanel}>
            <div className={classes.image}>
                {panoramaMode ?
                    // <iframe
                    //     width="100%"
                    //     height="100%"
                    //     allowFullScreen
                    //     style={{borderStyle: 'none'}}
                    //     src={"https://cdn.pannellum.org/2.5/pannellum.htm#panorama=" + currentPoint.properties.photo_url}
                    // />
                    <div id="panorama" />
                    :
                    <TransitionGroup>
                        <CSSTransition classNames={{
                            enter: classes.frameTransitionEnter,
                            exit: classes.frameTransitionExit
                        }} timeout={500} key={currentPoint.properties.photo_url}>
                            <img src={currentPoint.properties.photo_url}/>
                        </CSSTransition>
                    </TransitionGroup>
                }
            </div>
            <div className={classes.controls}>
                <div className={classes.buttons}>
                    <button onClick={backToCityLens}>
                        Back to CityLensWeb
                    </button>

                    <button onClick={onClose}>
                        Expand map
                    </button>

                    {prevPoint && (
                        <button
                            onClick={() => prevPoint && changePoint(prevPoint)}>
                            Backward
                        </button>
                    )}
                    {nextPoint && (
                        <button
                            onClick={() => nextPoint && changePoint(nextPoint)}> Forward </button>
                    )}
                    {
                        running ?
                            <button onClick={() => setRunning(false)}>Stop</button>
                            :
                            <button onClick={() => setRunning(true)}>Run</button>
                    }

                </div>
            </div>
            <div className={similarHidden ? classes.similarPanel + ' ' + classes.hidden : classes.similarPanel}>
                <button onClick={() => setSimilarHidden(!similarHidden)}>
                    {similarHidden ? 'Show similar tracks' : 'Hide similar tracks'}
                </button>
                <div className={classes.similar}>
                    {similar.length ?
                        similar.map(SimilarTrack)
                        :
                        <span>Stop to search for similar tracks</span>
                    }
                </div>
            </div>
            <div className={classes.info}>
                <dl>
                    <dt>Track</dt>
                    <dd>{properties.track_id}</dd>

                    <dt>Coordinates</dt>
                    <dd>POINT ({currentPoint.lngLat[0]}, {currentPoint.lngLat[1]})</dd>

                    <dt>Capture time</dt>
                    <dd>{currentPoint.properties.capture_time}</dd>

                    <dt>CityLens Frame ID</dt>
                    <dd>
                        <a target="blank_" href={`/frames/${properties.id}`}>
                            {properties.id}
                        </a>
                    </dd>
                </dl>
            </div>
        </div>
    );
};

interface SimilarTrackProps {
    id: string,
    key: string,
    url: string,
    date: string,
    lngLat: number[],
    onSwitch: (lngLat: number[], trackHint: string) => void,
}

function SimilarTrack(props: SimilarTrackProps) {
    return <div className={classes.similarTrack} onClick={() => props.onSwitch(props.lngLat, props.id)}>
        <img src={props.url}/>
        <label>{props.date}</label>
    </div>;
}
