const baseUrl = window.baseUrl ?? 'https://roadmap-wfs-api-production.api.2gis.ru/';

export async function getSimilarTracks(frameId: number) {
    const url = `${baseUrl}?SERVICE=WFS&VERSION=2.0.0&REQUEST=GetFeature&typeNames=similar_tracks&featureID=${frameId}&srsName=EPSG:4326&crsName=EPSG:4326`;
    return await fetch(url);
}

export async function getFrames(bbox: string) {
    const url = `${baseUrl}?SERVICE=WFS&VERSION=2.0.0&REQUEST=GetFeature&BBOX=${bbox}&srsName=EPSG:3395&crsName=EPSG:3395&MaxFeatures=100000&outputFormat=application/json`;
    return await fetch(url);
}
