import { throttle } from '@trufi/utils';
import { memo, useCallback, useContext, useEffect, useMemo, useState } from 'react';
import { BackendPointData } from '../../types';
import { findNearest } from '../../utils';
import { BackendDataContext } from '../BackendData/context';
import { MapContext } from '../map/context';
import * as classes from './styles.module.css';
import { useCamera } from './useCamera';

export interface PopupProps {
    lngLat: number[];
}

export const Popup = memo(({ lngLat }: PopupProps) => {
    const { map } = useContext(MapContext);
    const { find } = useContext(BackendDataContext);

    const firstPoint = useMemo(() => {
        const points = find({ lngLat, radius: 5000 });
        return findNearest(lngLat, points);
    }, [lngLat]);

    useCamera({ point: firstPoint });

    const [imageData, setImageData] = useState<BackendPointData | undefined>(
        firstPoint?.properties,
    );
    const [imageLoaded, setImageLoaded] = useState(false);
    const throttledSetImageData = useCallback(
        throttle((props: BackendPointData | undefined) => setImageData(props), 300),
        [],
    );
    useEffect(() => {
        setImageLoaded(false);
        throttledSetImageData(firstPoint?.properties);
    }, [firstPoint?.properties.photo_url]);

    if (!map || !firstPoint) {
        return null;
    }

    const offset = 15;
    const screenPoint = map.project(firstPoint.lngLat);

    return (
        <div
            className={classes.root}
            style={{
                top: `${screenPoint[1] + offset}px`,
                left: `${screenPoint[0] + offset}px`,
            }}
        >
            <img
                style={{ opacity: imageLoaded ? 1 : 0.5 }}
                src={imageData?.photo_url}
                onLoad={() => setImageLoaded(true)}
            />
        </div>
    );
});
