import {useEffect, useRef, useState} from 'react';
import {MapState} from '../../types';
import {BackendData} from '../BackendData';
import {FetchStatus} from '../fetchStatus';
import {MapProvider} from '../map';
import {Popup} from '../popup';
import {Track} from '../track';
import * as classes from './styles.module.css';

export const App = () => {
    const didMount = useRef(false);
    useEffect(() => {
        didMount.current = true;
    }, []);

    let initialProject = window.initialProject ?? 'Novosibirsk';
    let initialCenter = (window as any).projectList.find(option => option.name == initialProject)?.coords || [];


    const search = new URLSearchParams(window.location.search);
    const searchPoint = search.get('point');
    if (!didMount.current && searchPoint) {
        try {
            let coords = searchPoint.split(',').map(value => Number.parseFloat(value));
            initialCenter = coords;
            setTimeout(() => {
                setClickedPoint(coords);
            }, 3000);
        } catch (error) {
            console.log(error)
        }
    }

    const [project, setProject] = useState(initialProject);
    const [center, setCenter] = useState(initialCenter);
    const [centerAzimuth, setCenterAzimuth] = useState(0);

    const changeProject = (value: string) => {
        setProject(value);
        const newCenter = (window as any).projectList.find(option => option.name == value)?.coords || [];
        setCenter([...newCenter]);
    }

    const backToCityLens = () => {
        window.location.href = '/'
    }

    const initialMapState: MapState = {
        center,
        zoom: 15,
        bounds: {
            southWest: center,
            northEast: center,
        },
    };
    const [mapState, setMapState] = useState<MapState>(initialMapState);

    const [clickedPoint, setClickedPoint] = useState<number[] | undefined>();
    const changeClickedPoint = (point: number[] | undefined) => {
        if (point) {
            const search = new URLSearchParams();
            search.set("point", point.join(','))
            //window.location.search = search.toString();
            window.history.replaceState(null, document.title, window.location.pathname + '?' + search.toString())
        } else {
            console.log('remove point from url');
            window.history.replaceState(null, document.title, window.location.pathname)
        }
        setClickedPoint(point);
    }
    const [trackHint, setTrackHint] = useState<string | undefined>();
    const [movePoint, setMovePoint] = useState<number[] | undefined>();
    const mapClasses = clickedPoint === undefined ? classes.map : classes.map + ' ' + classes.minimized;

    return (
        <>
            <div id="map" className={mapClasses}></div>
            <BackendData mapState={mapState}>
                <MapProvider
                    initialState={initialMapState}
                    onClick={({lngLat}) => {
                        setTrackHint(undefined);
                        changeClickedPoint(lngLat)
                    }}
                    onMoveEnd={({center, zoom, bounds}) => setMapState({center, zoom, bounds})}
                    onMouseMove={({lngLat}) => setMovePoint(lngLat)}
                    onMoveStart={() => {
                    }}
                    center={center}
                    centerAzimuth={centerAzimuth}
                    minimized={clickedPoint !== undefined}
                >
                    <select className={classes.project} value={project}
                            onChange={event => changeProject(event.target.value)}>
                        {(window as any).projectList.map(option => (
                            <option key={option.name} value={option.name}>{option.name}</option>
                        ))}
                    </select>
                    {movePoint && <Popup lngLat={movePoint}/>}
                    {
                        clickedPoint ?
                            <Track
                                lngLat={clickedPoint}
                                trackHint={trackHint}
                                onClose={() => {
                                    setTrackHint(undefined);
                                    changeClickedPoint(undefined);
                                }}
                                backToCityLens={backToCityLens}
                                onSwitch={(newPoint, newTrack) => {
                                    setTrackHint(newTrack);
                                    changeClickedPoint(newPoint);
                                }}
                                onMove={point => {
                                    if (point) {
                                        setCenter(point.lngLat)
                                        setCenterAzimuth(point.properties.azimuth);
                                        setMovePoint(undefined);
                                    }
                                }}
                            />
                            :
                            <div className={classes.controls}>
                                <div className={classes.buttons}>
                                    <button onClick={backToCityLens}>Back to CityLensWeb</button>
                                </div>
                            </div>
                    }
                    <FetchStatus zoom={mapState.zoom}/>
                </MapProvider>
            </BackendData>
        </>
    );
};
