import { mapPointFromLngLat, mapPointToLngLat } from '@trufi/utils';
import KDBush from 'kdbush';
import { ReactNode, useEffect, useMemo, useState } from 'react';
import { MapState } from '../../types';
import { BackendDataContext, SearchPoint } from './context';
import { createLines } from './createLines';
import { createPoints } from './createPoints';
import { epsg3395to4326 } from './epsg3395';
import { BackendGeoJsonData, fetchTile } from './fetchTile';
import { TileCoords, viewportToTiles } from './viewportToTiles';

export interface BackendDataProps {
    mapState: MapState;
    children?: ReactNode;
}

export interface TileData {
    coords: TileCoords;
    data?: BackendGeoJsonData;
}

const tileKey = ({ x, y, zoom }: TileCoords) => `${x}_${y}_${zoom}`;

export const dataFetchingZoom = 13;

export const BackendData = (p: BackendDataProps) => {
    const [tilesData, setTilesData] = useState<{ [tileKey: string]: TileData }>({});

    const tileZoom = 14;

    const viewportTiles = useMemo(
        () =>
            tileZoom >= dataFetchingZoom
                ? viewportToTiles({ bounds: p.mapState.bounds, zoom: tileZoom })
                : [],
        [p.mapState.bounds, tileZoom],
    );

    useEffect(() => {
        if (p.mapState.zoom <= dataFetchingZoom) {
            return;
        }
        for (const coords of viewportTiles) {
            const key = tileKey(coords);
            if (tilesData[key]) {
                continue;
            }

            setTilesData((tilesData) => ({ ...tilesData, [key]: { coords } }));

            fetchTile({ coords }).then((data) => {
                changeProjectionToLngLat(data);
                setTilesData((tilesData) => {
                    return {
                        ...tilesData,
                        [key]: {
                            coords,
                            data,
                        },
                    };
                });
            });
        }
    }, [viewportTiles]);

    const { lines, points, kdbush, isFetching } = useMemo(() => {
        const zoomData: BackendGeoJsonData = {
            type: 'FeatureCollection',
            features: Object.values(tilesData)
                .filter((tile) => tile.coords.zoom === tileZoom)
                .map((tile) => tile.data?.features ?? [])
                .flat(),
        };
        const lines = createLines(zoomData);
        const points = createPoints(zoomData);
        const kdbush = new KDBush(
            points,
            (p) => p.x,
            (p) => p.y,
            64,
            Int32Array,
        );
        const isFetching = viewportTiles.some((coords) => {
            const key = tileKey(coords);
            return tilesData[key]?.data === undefined;
        });

        return { lines, points, kdbush, isFetching };
    }, [tilesData]);

    const find = ({ lngLat, radius }: { lngLat: number[]; radius: number }) => {
        const [x, y] = mapPointFromLngLat(lngLat);
        const findedPoints = kdbush.within(x, y, radius).map((index) => points[index]);

        findedPoints.sort((a, b) => {
            const sqrDistanceA = (a.x - x) ** 2 + (a.y - y) ** 2;
            const sqrDistanceB = (b.x - x) ** 2 + (b.y - y) ** 2;
            return sqrDistanceA - sqrDistanceB;
        });

        return findedPoints.map(({ x, y, properties }) => ({
            lngLat: mapPointToLngLat([x, y]),
            properties,
        }));
    };

    const findLinePoints = (point: SearchPoint) => {
        for (const line of lines.features) {
            if (line.properties.track_id === point.properties.track_id) {
                return line.properties.points;
            }
        }
    };

    return (
        <BackendDataContext.Provider value={{ find, findLinePoints, lines, isFetching }}>
            {p.children}
        </BackendDataContext.Provider>
    );
};

function changeProjectionToLngLat(data: BackendGeoJsonData) {
    data.features.forEach(({ geometry }) => {
        switch (geometry.type) {
            case 'Point':
                geometry.coordinates = epsg3395to4326.forward(geometry.coordinates);
                break;
            default:
                console.error(`Unknown geojson type ${geometry.type} at lngLat converting`);
        }
    });
}
