import type { Feature, FeatureCollection, LineString, Point } from 'geojson';
import { BackendPointData } from '../../types';
import { BackendGeoJsonData } from './fetchTile';

export interface LineProperties {
    track_id: string;
    points: Feature<Point, BackendPointData>[];
}

export type LinesGeoJson = FeatureCollection<LineString, LineProperties>;

export function createLines(data: BackendGeoJsonData): LinesGeoJson {
    const pointsByLines = new Map<string, Feature<Point, BackendPointData>[]>();

    data.features.forEach((feature) => {
        const id = feature.properties.track_id;
        const line = pointsByLines.get(id);
        if (line) {
            line.push(feature);
        } else {
            pointsByLines.set(id, [feature]);
        }
    });

    const lines: LinesGeoJson = {
        type: 'FeatureCollection',
        features: [],
    };

    pointsByLines.forEach((points, id) => {
        points.sort((a, b) =>
            new Date(a.properties.capture_time) < new Date(b.properties.capture_time) ? -1 : 1,
        );

        lines.features.push({
            type: 'Feature',
            properties: {
                track_id: id,
                points,
            },
            geometry: {
                type: 'LineString',
                coordinates: points.map((point) => point.geometry.coordinates),
            },
        });
    });

    return lines;
}
