import type {FeatureCollection, Point} from 'geojson';
import {BackendPointData} from '../../types';
import {epsg3395to4326} from './epsg3395';
import {TileCoords} from './viewportToTiles';
import {getFrames} from "../../wfs";

export type BackendGeoJsonData = FeatureCollection<Point, BackendPointData>;

export async function fetchTile({coords}: { coords: TileCoords }): Promise<BackendGeoJsonData> {
    const {southWest, northEast} = coords.bounds;
    const bbox = [
        ...epsg3395to4326.inverse(southWest),
        ...epsg3395to4326.inverse(northEast)
    ].join(',');


    try {
        const res = await getFrames(bbox);
        return await res.json() as any;
    } catch (e) {
        console.error(e);
        return {features: [], type: "FeatureCollection"}
    }

}
