import { mapPointFromLngLat, mapPointToLngLat } from '@trufi/utils';
import { LngLatBounds } from '../../types';

export interface TileCoords {
    x: number;
    y: number;
    zoom: number;
    bounds: LngLatBounds;
}

export function viewportToTiles({ bounds, zoom }: { bounds: LngLatBounds; zoom: number }) {
    zoom = Math.floor(zoom);

    const topRight = mapPointFromLngLat(bounds.northEast);
    const bottomLeft = mapPointFromLngLat(bounds.southWest);

    const topRightTile = getTileByPointAndZoom(topRight, zoom);
    const bottomLeftTile = getTileByPointAndZoom(bottomLeft, zoom);

    const tiles: TileCoords[] = [];

    for (let x = bottomLeftTile.x; x <= topRightTile.x; x++) {
        for (let y = bottomLeftTile.y; y <= topRightTile.y; y++) {
            const leftTopPoint = tileCoordsToMapPoint(x, y, zoom);
            const rightBottomPoint = tileCoordsToMapPoint(x + 1, y + 1, zoom);

            const bounds: LngLatBounds = {
                northEast: mapPointToLngLat(leftTopPoint),
                southWest: mapPointToLngLat(rightBottomPoint),
            };

            tiles.push({ x, y, zoom, bounds });
        }
    }
    return tiles;
}

function getTileByPointAndZoom(point: number[], zoom: number) {
    const offset = 2147483648; //  2^31
    const tileSize = tileZoomToSize(zoom);

    return {
        x: Math.floor((point[0] + offset) / tileSize),
        y: Math.floor((point[1] + offset) / tileSize),
        zoom,
    };
}

function tileCoordsToMapPoint(x: number, y: number, zoom: number) {
    const mapTileSize = tileZoomToSize(zoom);
    const offset = 2147483648; //  2^31

    return [x * mapTileSize - offset, y * mapTileSize - offset];
}

function tileZoomToSize(tileZoom: number): number {
    return 2 ** (32 - tileZoom);
}
