import {mapPointFromLngLat} from '@trufi/utils';
import {BackendPointData} from './types';
import {lineArc} from "@turf/turf";

export interface TrackPoint {
    lngLat: number[];
    properties: BackendPointData
}

export function findNearest(
    lngLat: number[],
    points: Array<{ lngLat: number[]; properties: BackendPointData }>,
    trackHint?: string
): TrackPoint | undefined {
    let nearest: TrackPoint | undefined;
    let minDistance = Infinity;

    const [x, y] = mapPointFromLngLat(lngLat);

    for (const point of points) {
        if (trackHint && point.properties.track_id != trackHint) {
            continue
        }
        const [px, py] = mapPointFromLngLat(point.lngLat);
        const sqrDistance = (px - x) ** 2 + (py - y) ** 2;
        if (sqrDistance < minDistance) {
            nearest = point;
            minDistance = sqrDistance;
        }
    }

    return nearest;
}


export function createArcGeometry(
    lngLatCenter: number[],
    rotation: number,
    fov: number,
    radiusInMeters: number,
) {
    const lineString = lineArc(
        lngLatCenter,
        radiusInMeters / 1000,
        rotation - fov / 2,
        rotation + fov / 2,
    );
    const ring = lineString.geometry.coordinates;
    if (fov < 360) {
        ring.unshift(lngLatCenter);
        ring.push(lngLatCenter);
    }
    return [ring];
}