import { useContext } from 'react';
import { dataFetchingZoom } from '../BackendData';
import { BackendDataContext } from '../BackendData/context';
import * as classes from './styles.module.css';

export const FetchStatus = (p: { zoom: number }) => {
    const { isFetching } = useContext(BackendDataContext);

    if (p.zoom <= dataFetchingZoom) {
        return <div className={classes.root}>Please zoom in to view the data</div>;
    }

    if (isFetching) {
        return <div className={classes.root}>Loading data...</div>;
    }

    return null;
};
