import { Feature, Point } from 'geojson';
import { createContext } from 'react';
import { BackendPointData } from '../../types';
import { LinesGeoJson } from './createLines';

export interface SearchPoint {
    lngLat: number[];
    properties: BackendPointData;
}

export interface BackendData {
    find: (p: { lngLat: number[]; radius: number }) => SearchPoint[];
    findLinePoints: (p: SearchPoint) => Feature<Point, BackendPointData>[] | undefined;
    isFetching: boolean;
    lines: LinesGeoJson;
}

export const BackendDataContext = createContext<BackendData>({} as any);
