import { mapPointFromLngLat } from '@trufi/utils';
import { BackendPointData } from '../../types';
import { BackendGeoJsonData } from './fetchTile';

export interface PointData {
    x: number;
    y: number;
    properties: BackendPointData;
}

export function createPoints(data: BackendGeoJsonData): PointData[] {
    const points: PointData[] = [];

    data.features.forEach(({ geometry, properties }) => {
        if (geometry.type !== 'Point') {
            return;
        }

        const mapPoint = mapPointFromLngLat(geometry.coordinates);

        points.push({
            x: mapPoint[0],
            y: mapPoint[1],
            properties,
        });
    });

    return points;
}
